import {
  ISolicitacaoFormView,
  ISolicitacaoFormController,
} from "../../contracts/Solicitacao/SolicitacaoFormContract";
import axios from "../../plugins/axios";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

export class SolicitacaoFormController implements ISolicitacaoFormController {
  _view: ISolicitacaoFormView;

  _produtos = [
    {
      codigo: "",
      nome: "",
      dataNecessidade: "",
      um: "",
      justificativa: "",
    },
  ];

  _lista = [
    {
      numero: "",
      produto: "",
      descricao: "",
      quantidade: "",
      dataNecessidade: "",
      um: "",
      justificativa: "",
      acao: [new HeaderAction("", "", "", "", "")],
    },
  ];

  constructor(view: ISolicitacaoFormView) {
    this._view = view;
  }

  addProdutoLista(produto: {
    codigo: "";
    quantidade: "";
    nome: "";
    dataNecessidade: "";
    justificativa: "";
    um: "";
  }): void {
    if (!produto.codigo || !produto.quantidade) {
      this._view.showMsg("Preencha o Produto e Quantidade.", 0);
    } else if (parseInt(produto.quantidade) <= 0) {
      this._view.showMsg("Quantidade inválida.", 0);
    } else if (
      this._lista.filter((e) => e.produto === produto.codigo).length > 0
    ) {
      this._view.showMsg("Produto inválido.", 0);
    } else if (!produto.dataNecessidade) {
      this._view.showMsg("Data Necessidade inválida.", 0);
    } else if (!produto.justificativa) {
      this._view.showMsg("Motivo inválido.", 0);
    } else {
      let produtoAux = this._produtos.filter(
        (e) => e.codigo === produto.codigo
      )[0];

      this._lista.push({
        numero: (this._lista.length + 1).toLocaleString("pt-BR", {
          minimumIntegerDigits: 3,
        }),
        produto: produto.codigo,
        descricao: produto.nome,
        quantidade: produto.quantidade,
        um: produto.um,
        dataNecessidade: produto.dataNecessidade,
        justificativa: produto.justificativa,
        acao: [
          new HeaderAction(
            "mdi-eye",
            "primary",
            "Visualizar",
            "visualizarProduto",
            "200px"
          ),
          new HeaderAction(
            "mdi-delete",
            "primary",
            "Remover",
            "removerProduto",
            "200px"
          ),
        ],
      });

      this._view.showProdutos(this._lista);
      this._view.limparProduto();
    }
  }

  removeProdutoLista(codigo: string): void {
    let novoArray = [
      {
        numero: "0",
        produto: "",
        descricao: "",
        quantidade: "",
        dataNecessidade: "",
        um: "",
        justificativa: "",
        acao: [new HeaderAction("", "", "", "", "")],
      },
    ];
    novoArray = [];
    let i = 1;
    this._lista.forEach((element) => {
      if (element.produto != codigo) {
        element.numero = i.toLocaleString("pt-BR", { minimumIntegerDigits: 3 });
        novoArray.push(element);
        i++;
      }
    });
    this._lista = novoArray;
    this._view.showProdutos(this._lista);
  }

  getProduto(codigo: string): any {
    if (codigo) {
      return this._produtos.filter((e) => e.codigo === codigo)[0];
    } else {
      return { quantidade: "" };
    }
  }

  carregarDtos(): void {
    this._view.loading(true);

    axios
      .get("Solicitacao/GetParametros")
      .then((response: any) => {
        this._view.loading(false);
        if (response.data.sucesso) {
          let data = response.data.tRetorno;
          data.produtos.forEach(
            (element: { pesquisa: string; codigo: string; nome: void }) => {
              element.pesquisa = element.codigo + " - " + element.nome;
            }
          );
          this._produtos = data.produtos;
          this._view.carregarDtos(
            data.filiais,
            data.centroCustos,
            data.produtos,
            data.fretes,
            data.colaboradors
          );
          this._lista = [];
        } else {
          this._view.showMsg(response.data.message, 0);
        }
      })
      .catch((e: any) => {
        this._view.loading(false);
        this._view.showMsg(e, 0);
      });
  }

  private validarSolicitacao(solicitacao: any): boolean {
    if (!solicitacao.solicitante) {
      this._view.showMsg("A propriedade Colaborador é obrigatória.", 0);
      return false;
    }
    if (!solicitacao.filial) {
      this._view.showMsg("A propriedade Filial é obrigatória.", 0);
      return false;
    }
    if (!solicitacao.centroCusto) {
      this._view.showMsg("A propriedade Departamento é obrigatória.", 0);
      return false;
    }
    // if (!solicitacao.frete) {
    //   this._view.showMsg("A propriedade Frete é obrigatória.", 0);
    //   return false;
    // }
    if (!solicitacao.solicitante) {
      this._view.showMsg("A propriedade Data Vencimento é obrigatória.", 0);
      return false;
    }
    // if (!solicitacao.descricao) {
    //   this._view.showMsg("A propriedade Descrição é obrigatória.", 0);
    //   return false;
    // }
    console.log(solicitacao.produtos);
    if (solicitacao.produtos.length <= 0) {
      this._view.showMsg("Solicite pelo menos um produto.", 0);
      return false;
    }
    if (!solicitacao.user) {
      this._view.showMsg("A propriedade Usuário é obrigatória.", 0);
      return false;
    }
    return true;
  }

  salvar(solicitacao: any): void {
    if (this.validarSolicitacao(solicitacao)) {
      this._view.loading(true);

      axios
        .post("Solicitacao", solicitacao)
        .then((response: any) => {
          this._view.loading(false);
          if (response.data.sucesso) {
            this._view.showMsg(response.data.message, 1);
          } else {
            this._view.showMsg(response.data.message, 0);
          }
        })
        .catch((e: any) => {
          this._view.loading(false);
          this._view.showMsg(e, 0);
        });
    }
  }

  update(id: string, solicitacao: any): void {
    if (this.validarSolicitacao(solicitacao)) {
      this._view.loading(true);

      axios
        .put("Solicitacao/" + id, solicitacao)
        .then((response: any) => {
          this._view.loading(false);
          if (response.data.sucesso) {
            this._view.showMsg("Salvo com sucesso.", 1);
          } else {
            this._view.showMsg(response.data.message, 0);
          }
        })
        .catch((e: any) => {
          this._view.loading(false);
          this._view.showMsg(e, 0);
        });
    }
  }

  getProdutoForEditar(id: string): void {
    this._view.loading(true);

    axios
      .get("Solicitacao/ById/" + id)
      .then((response: any) => {
        this._view.loading(false);
        if (response.data) {
          response.data.dataVencimento = response.data.dataVencimento.split(
            "T"
          )[0];

          this._lista = [];
          let i = 1;
          response.data.itens.forEach((element: any) => {
            let produto = {
              numero: i.toLocaleString("pt-BR", { minimumIntegerDigits: 3 }),
              produto: element.codProduto,
              descricao: this._produtos.filter(
                (e) => e.codigo == element.codProduto
              )[0].nome,
              quantidade: element.quantidade,
              um: this._produtos.filter(
                (e) => e.codigo == element.codProduto
              )[0].um,
              dataNecessidade: element.dataNecessidadeFormat,
              justificativa: element.justificativa,
              acao: [
                new HeaderAction(
                  "mdi-eye",
                  "primary",
                  "Visualizar",
                  "visualizarProduto",
                  "200px"
                ),
                new HeaderAction(
                  "mdi-delete",
                  "primary",
                  "Remover",
                  "removerProduto",
                  "200px"
                ),
              ],
            };

            this._lista.push(produto);
            i++;
          });
          this._view.showProdutoForEditar(response.data, this._lista);
        } else {
          this._view.showMsg("Não encontrado. ", 0);
        }
      })
      .catch((e: any) => {
        console.log(e);
        this._view.loading(false);
        this._view.showMsg(e, 0);
      });
  }
}
