


































































































































































































































































































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import Component from "vue-class-component";
import { Watch } from "vue-property-decorator";

import { Display } from "../../configuration/display";
import { Storage } from "../../configuration/storage";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  ISolicitacaoFormView,
  ISolicitacaoFormController
} from "../../contracts/Solicitacao/SolicitacaoFormContract";
import { SolicitacaoFormController } from "../../controllers/Solicitacao/SolicitacaoFormController";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class SolicitacaoForm extends Vue
  implements ISolicitacaoFormView {
  _controller?: ISolicitacaoFormController;

  dataVencimentoMenu = false;
  dataNecessidadeMenu = false;
  dataNecessidadeItemMenu = false;

  isMobile = false;

  isSucesso = false;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  visualizarProdutoDialog = false;
  justificativaProduto = "";

  itensFiliais = [];
  itensCentroCustos = [];
  itensProdutos = [
    {
      nome: "",
      codigo: "",
      quantidade: ""
    }
  ];
  itensFretes = [];
  itensColaboradores = [];

  produtoSelecionado = {
    quantidade: "",
    dataNecessidade: "",
    um: "-",
    justificativa: ""
  };

  headers = new Headers([
    new Header("Número", "numero", false, false),
    new Header("Produto/Serviço", "produto", false, false),
    new Header("Descrição", "descricao", false, false),
    new Header("Quantidade", "quantidade", false, false),
    new Header("Necessidade", "dataNecessidade", false, false),
    new Header("Ações", "acao", true, false)
  ]);

  idSolicitacao = "";

  itens = [
    {
      numero: "0",
      produto: "",
      descricao: "",
      quantidade: "",
      um: "-",
      dataNecessidade: "",
      justificativa: "",
      acao: [new HeaderAction("", "", "", "", "")]
    }
  ];

  colaborador: any = {};

  solicitacao = {
    solicitante: "",
    filial: "",
    centroCusto: "",
    frete: " N/A",
    dataVencimento: "",
    descricao: "",
    produtos: this.itens,
    user: 0
  };

  produto = {
    produto: "",
    quantidade: "",
    dataNecessidade: "",
    um: "-",
    justificativa: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.isSucesso) {
      this.$router.push("/solicitacoes");
    }
  }

  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    if (modo == 1) {
      this.isSucesso = true;
    }
    this.dialogMessage = true;
  }

  saveSolicitacao() {
    this.solicitacao.produtos = this.itens;
    this.solicitacao.user = parseInt(Storage.get("User")!);
    this._controller!.salvar(this.solicitacao);
  }

  updateSolicitacao() {
    this.itens.forEach(element => {
      element.quantidade = element.quantidade.toString().replace(".", ",");
    });
    this.solicitacao.produtos = this.itens;
    this.solicitacao.user = parseInt(Storage.get("User")!);
    this._controller!.update(this.idSolicitacao, this.solicitacao);
  }

  removerProduto(item: any) {
    this._controller!.removeProdutoLista(item.produto);
  }

  visualizarProduto(item: any) {
    this.justificativaProduto = "Motivo: " + item.justificativa;
    this.visualizarProdutoDialog = true;
  }

  fecharVisualizarProdutoDialog() {
    this.visualizarProdutoDialog = false;
  }

  add() {
    this.produtoSelecionado.quantidade = this.produto.quantidade;
    this.produtoSelecionado.dataNecessidade = this.produto.dataNecessidade;
    this.produtoSelecionado.justificativa = this.produto.justificativa;
    this._controller!.addProdutoLista(this.produtoSelecionado);
  }

  showProdutos(produtos: any) {
    this.itens = produtos;
  }

  carregarDtos(
    itensFiliais: [],
    itensCentroCustos: [],
    itensProdutos: [],
    itensFretes: [],
    itensColaboradores: []
  ): void {
    this.itensFiliais = itensFiliais;
    this.itensCentroCustos = itensCentroCustos;
    this.itensProdutos = itensProdutos;
    this.itensFretes = itensFretes;
    this.itensColaboradores = itensColaboradores;

    if (this.$route.params.id) {
      this.idSolicitacao = this.$route.params.id;
      this._controller!.getProdutoForEditar(this.idSolicitacao);
    } else {
      if (this.colaborador) {
        this.solicitacao.solicitante = this.colaborador.codigo;
        this.solicitacao.filial = this.colaborador.filial;
        this.solicitacao.centroCusto = this.colaborador.centroCusto;
      }
    }
  }

  showProdutoForEditar(item: any, itens: any) {
    this.solicitacao = item;
    this.itens = itens;
  }

  limparProduto() {
    this.produto = {
      produto: "",
      quantidade: "",
      justificativa: "",
      um: "-",
      dataNecessidade: this.solicitacao.dataVencimento
    };
  }

  created() {
    this._controller = new SolicitacaoFormController(this);

    this.colaborador = JSON.parse(Storage.get("Colaborador")!!);

    this._controller.carregarDtos();

    this.isMobile = Display.isMobile();
    this.itens = [];
  }

  @Watch("produto.produto")
  onPropertyChanged(value: string, oldValue: string) {
    this.produtoSelecionado = this._controller!.getProduto(
      this.produto.produto
    );
  }

  @Watch("solicitacao.dataVencimento")
  produtoChange(value: string, oldValue: string) {
    this.produto.dataNecessidade = this.solicitacao.dataVencimento;
  }
}
